import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import validationKategori from "../../Validations/validationKategori";
import { renderField, renderFieldSelect } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";
import { SkeletonLoading } from "../../GlobalComponent/function";

const maptoState = (state) => {
  return {
    initialValues: {
      kodeKategori: state.provinsi.editKategori.kodeKategori,
      kodeGroup: state.provinsi.editKategori.kodeGroup,
      keterangan: state.provinsi.editKategori.keterangan,
    },
  };
};

class FormKategoriEdit extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listKelompok: [],
    };
  }

  componentDidMount() {
    AxiosMasterGet("group/get/all").then((res) =>
      this.setState({
        listKelompok: res.data,
      })
    );
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-4 col-lg-4">
            {String(this.state.listKelompok) !== "" ? (
              <Field
                component={renderFieldSelect}
                label="Kode Kategori"
                name="kodeGroup"
                normalize={(value) => value && value.toUpperCase()}
                className="form-control"
                options={this.state.listKelompok.map((obj) => {
                  var payload = {
                    value: obj.kode_group,
                    name: obj.kode_group,
                  };
                  return payload;
                })}
              ></Field>
            ) : (
              <SkeletonLoading label="Kode Kategori" />
            )}
          </div>
          <div className="col-md-4 col-lg-4">
            <Field
              name="kodeKategori"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Kode Jenis"
              readOnly={true}
              limit={10}
            />
          </div>

          <div className="col-md-4 col-lg-4 d-none">
            <Field
              name="kodeIntern"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Kode Intern"
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <Field
              name="keterangan"
              type="text"
              normalize={(value) => value && value.toUpperCase()}
              component={renderField}
              label="Nama Jenis"
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button
                className="btn btn-primary"
                disabled={this.props.submitting}
                type="submit"
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                Simpan
                <i className="fa fa-paper-plane ml-3"></i>
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormKategoriEdit = reduxForm({
  form: "KategoriEdit",
  validate: validationKategori,
  enableReinitialize: true,
})(FormKategoriEdit);
export default connect(maptoState, null)(FormKategoriEdit);
